.pay {
    width: 50%;
    margin: auto;
    margin-top: 48px;
    text-align: center;
    padding: 10px 50px;
}

span {
    font-size: 16px;
    margin: 0px;
    padding: 0px;
}

.pay h5 {
    color: rgb(77, 77, 77);
    line-height: 32px;
    font-size: 18px;
}

.pay img {
    width: 100%;
}

@media(max-width: 700px){
    .pay {
        width: 97%;
        padding: 10px 20px;
    }
}